footer {
  padding-top: 3%;
  width: 100%;
  height: 400px;
  background-color: #000;
}

footer .content {
  width: 80%;
  height: 80%;
  margin: 0 10%;
}

footer .content .left-menu {
  width: 60%;
  height: auto;
  float: left;
}

.left-menu h3 {
  text-transform: uppercase;
  font-size: 12px;
  color: #919496;
  font-weight: bold;
}

.left-menu ul {
  padding: 0;
}

.left-menu li {
  list-style: none;
  margin: 15px 0;
}

.spotify-logo,
.perusahaan,
.komunitas,
.tautan-berguna {
  width: 25%;
  height: auto;
  float: left;
  color: white;
}

.spotify-logo span {
  vertical-align: super;
  font-size: 19px;
  font-weight: bold;
}

footer .content .right-menu {
  width: 40%;
  height: auto;
  float: right;
  text-align: right;
}

.right-menu .instagram,
.right-menu .twitter,
.right-menu .facebook {
  height: 24px;
  width: 24px;
  background-color: #222326;
  color: white;
  padding: 13px;
  margin: 10px;
  border-radius: 50%;
}

footer .country {
  width: 100%;
  height: auto;
  float: left;
  color: white;
  text-align: right;
  margin-bottom: -6px;
}

footer .country span {
  vertical-align: super;
  font-size: 12.5px;
  color: #919496;
  margin-right: 5px;
}

footer .bottom {
  width: 80%;
  height: 20%;
  margin-left: 10%;
  margin-right: 10%;
}

footer .bottom .left-bottom {
  width: 50%;
  height: auto;
  float: left;
}

.bottom .left-bottom ul {
  padding: 0;
}

.bottom .left-bottom li {
  list-style: none;
  display: inline;
  margin-right: 20px;
  color: #919496;
  font-size: 12.5px;
  cursor: pointer;
}

.bottom .left-bottom li:hover {
  color: #1db954;
}

.bottom .right-bottom {
  width: 50%;
  height: 18px;
  float: right;
  font-size: 12.5px;
  color: #919496;
  text-align: right;
  padding: 16px 0;
}

@media only screen and (max-width: 990px) {
  footer {
    height: 525px;
  }

  footer .content .left-menu {
    width: 100%;
  }

  footer .content .right-menu {
    width: 100%;
    text-align: left;
    margin-bottom: 20px;
  }

  .spotify-logo {
    width: 100%;
    margin-bottom: 30px;
  }

  .perusahaan,
  .komunitas,
  .tautan-berguna {
    width: calc(100% / 3);
  }

  .right-menu .instagram,
  .right-menu .twitter,
  .right-menu .facebook {
    height: 20px;
    width: 20px;
    margin: 120px 10px 10px 10px;
  }
}

@media only screen and (max-width: 699px) {
  footer {
    height: 700px;
  }

  .perusahaan,
  .komunitas,
  .tautan-berguna {
    width: 50%;
    margin-bottom: 30px;
  }

  footer .content,
  footer .bottom {
    width: 90%;
    margin: 0 5%;
  }

  footer .country span,
  .bottom .left-bottom li,
  .bottom .right-bottom {
    font-size: 10px;
  }

  .bottom .left-bottom li {
    margin-right: 10px;
  }

  footer .bottom .left-bottom {
    width: 65%;
  }

  footer .bottom .right-bottom {
    width: 35%;
    height: 18px;
    vertical-align: super;
    padding: 0;
    margin: 16px 0;
  }
}



